"use client"

import { useReportStore } from "@/stores/reportStore"
import { Button } from "@/components/ui"

export function ScreenshotPopup() {
  const { screenshotPopup, closeScreenshotPopup } = useReportStore()

  if (!screenshotPopup.isOpen || !screenshotPopup.imageData) {
    return null
  }

  const handleDownload = () => {
    if (!screenshotPopup.imageData) return

    const link = document.createElement("a")
    link.href = screenshotPopup.imageData
    link.download = `screenshot_${screenshotPopup.playerName}_${screenshotPopup.reportId}_${Date.now()}.jpg`
    link.click()
  }

  return (
    <div
      className="fixed inset-0 z-[100] flex items-center justify-center bg-black/90 backdrop-blur-sm"
      onClick={closeScreenshotPopup}
    >
      <div
        className="relative max-w-[90vw] max-h-[90vh] flex flex-col bg-bg-secondary rounded-lg shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* Header */}
        <div className="flex items-center justify-between px-6 py-4 border-b border-border">
          <div>
            <h3 className="text-lg font-semibold text-text-primary">Screenshot Captured</h3>
            <p className="text-sm text-text-secondary">
              From: {screenshotPopup.playerName}  Report #{screenshotPopup.reportId}
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button
              variant="secondary"
              size="sm"
              onClick={handleDownload}
            >
              <svg className="w-4 h-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-4l-4 4m0 0l-4-4m4 4V4" />
              </svg>
              Download
            </Button>
            <button
              onClick={closeScreenshotPopup}
              className="text-text-tertiary hover:text-text-primary transition-colors"
            >
              <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
        </div>

        {/* Image */}
        <div className="flex-1 overflow-auto p-4">
          <img
            src={screenshotPopup.imageData}
            alt="Screenshot"
            className="max-w-full max-h-[calc(90vh-120px)] mx-auto rounded-lg"
          />
        </div>
      </div>
    </div>
  )
}